  font-size: 30px;
}

#quote {
  font-family: "Dawning of a New Day", cursive;
  font-size: 40px;
}

#goalDeleteBtn {
  background-color: teal;
  text-align: center;
  font-size: 16px;
  border: 1px solid #3c0a4d;
  height: 8%;
  width: 40%;
  margin: 3%;
  border-radius: 4px;
  box-shadow: 9px 10px 52px -12px rgba(0, 0, 0, 0.75);
  color: white;
}

.goalsSubmitBtn {
  background-color: teal;
  text-align: center;
  font-size: 16px;
