}
.inspoImg {
  width: 40%;
}
.goalsUlPost {
  text-align: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.ulNavBar {
  background-color: white;
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  margin: 0;
  padding: 1%;
  top: 0;
  left: 0;
  box-shadow: -1px 7px 21px -1px rgba(0, 0, 0, 0.14);
}
.liNavBar {
  color: #81b1f9;
  text-decoration: none;
  text-align: center;
  margin: 0 auto;
  width: 5%;
  font-size: 25px;
}
.headerLink:hover {
  font-size: 30px;
  transition: ease-in 100ms;
  font-weight: 700;
}

.headerLink {
  text-decoration: none;
  color: #81b1f9;
  margin: 0;
}

.journalForm {
  margin: 0 auto;
